<template>
    <div class="Search" :class="{expanded : searchFocused}">
        <input v-model="searchText" class="search-input" type="text" placeholder="Search"
               v-on:focus="searchFocused = true">
        <!--        v-on:blur="searchFocused = false"-->
        <button class="search-submit"><img src="../assets/search.svg"></button>
        <div id="searchResults" v-if="searchFocused && !navigationMode">
            <div class="searchResult" v-for="result in dynamicResults" :key="result.name">
                <div class="resultName">{{result.name}}</div>
                <div class="resultSubtext">{{result.subtext}}</div>
            </div>
            Not finding what you want?

        </div>
        <navigation v-if="navigationMode" @close="closeNavigation"></navigation>


    </div>
</template>

<script>
	import Navigation from '@/components/Navigation'

	export default {
		name: 'Search',
		components: {Navigation},
		props: {
			msg: String
		},
		data: function () {
			return {
				searchFocused: false,
				searchText: '',
				results: [
					{name: 'Palace Island', subtext: 'island'},
					{name: 'Royal Fungal Gardens', subtext: 'area of interest'},
					{name: 'The Royal Palace', subtext: 'building'},
					{name: 'The Royal Palace Port, East', subtext: 'port'},
					{name: 'The Royal Palace Port, West', subtext: 'port'},
					{name: 'Trash Intake facility', subtext: 'building'}
				],
				navigationMode: false
			}
		},
		computed: {
			dynamicResults: function () {
				let searchResults = []
				if (this.searchText == '') {
					return searchResults
				}
				for (let result of this.results) {
					if (result.name.includes(this.searchText)) {
						searchResults.push(result)
					}
				}


				return searchResults

			}
		},
		methods: {
			searchFocus: function (event) {
				console.log(event)

			},
			closeNavigation: function () {
				this.navigationMode = false

			}
		}

	}
</script>

