<template>
    <div id="MapContainer">
        <canvas id="MapCanvasMain" resize="true" v-on:mousedown="mouseDown" v-on:wheel="mouseWheel"
                v-on:mousemove="hover"></canvas>
        <div id="debugInfo" v-on:click="reset">
            <strong>Debug Info</strong>: <br>
            Zoom Level: {{ Math.round(zoomLevel*100)/100 }}
            <br>
            Mouse: {{ hit }}
        </div>
        <!--        <pinned-overlay x-pos=250 y-pos=500></pinned-overlay>-->

    </div>
</template>

<script>
	// stable zoom based on https://codepen.io/hichem147/pen/dExxNK

	import * as paper from 'paper'
	import islands from '../assets/clarance_islands_arranged_3.svg'

    import places from '../data/places'

	// import pinnedOverlay from '@/components/pinnedOverlay'

	export default {
		name: 'Map',
		props: {
			msg: String
		},
		components: {
			// pinnedOverlay
		},
		data() {
			return {
				path: null,
				scope: null,
				zoomLevel: null,
				// name: 'loading',
				hit: 'nothing',
				previouslySelected: {
					item: null,
					color: null,
					width: null,
                    shadowColor: null,
                    shadowBlur: null
				}

			}
		},
		methods: {
			reset() {
				this.scope.project.activeLayer.removeChildren()
			},
			pathCreate(scope) {
				scope.activate()
				return new paper.Path({
					strokeColor: '#000000',
					strokeJoin: 'round',
					strokeWidth: 1.5
				})

			},
			createTool(scope) {
				scope.activate()
				return new paper.Tool()
			},
            isSelectable(name) {

				for (let prefix of places['selectable-prefixes']) {
					if (name.includes(prefix)) {
						return true
                    }
                }
				return false


            },


			hover(event) {

				//restore old first
				if (this.previouslySelected.item != null) {
					this.previouslySelected.item.strokeColor = this.previouslySelected.color
					this.previouslySelected.item.strokeWidth = this.previouslySelected.width
					this.previouslySelected.item.shadowColor = this.previouslySelected.shadowColor
					this.previouslySelected.item.shadowBlur = this.previouslySelected.shadowBlur

				}


				//do new

				let mousePosition = new paper.Point(event.offsetX, event.offsetY)

				let viewPosition = this.scope.view.viewToProject(mousePosition)
				let hitResult = this.scope.project.activeLayer.children[0].hitTest(viewPosition)
				if (hitResult != null && this.isSelectable(hitResult.item.name)) {
					this.previouslySelected.item = hitResult.item
					this.previouslySelected.color = hitResult.item.strokeColor
					this.previouslySelected.width = hitResult.item.strokeWidth
                    this.previouslySelected.shadowBlur = hitResult.item.shadowBlur
                    this.previouslySelected.shadowColor = hitResult.item.shadowBlur

					hitResult.item.strokeWidth = 0.2
					hitResult.item.strokeColor = '#2e2e2e'
                    hitResult.item.shadowColor = '#2e2e2e'
                    hitResult.item.shadowBlur = 5
					this.hit = hitResult.item.name
				} else {
					this.hit = 'nothing'
				}


			},
			mouseDown() {
				// TODO: Investigate if we can avoid creating a tool every time this is invoked

				let self = this

				this.tool = this.createTool(this.scope)
				this.tool.onMouseDown = () => {
				}


				this.tool.onMouseDrag = function (event) {
					let pan_offset = event.point.subtract(event.downPoint)
					self.scope.view.center = self.scope.view.center.subtract(pan_offset)
					self.name = self.scope.view.center
				}

				this.tool.onMouseUp = () => {
				}

			},
			mouseWheel(event) {
				let newZoom = this.scope.view.zoom
				let oldZoom = this.scope.view.zoom

				if (event.deltaY > 0) {
					newZoom = this.scope.view.zoom * 1.05
				} else {
					newZoom = this.scope.view.zoom * 0.95
				}
				if (newZoom > 8.0 || newZoom < 0.1) {
					event.preventDefault()
					return
				}


				let beta = oldZoom / newZoom

				let mousePosition = new paper.Point(event.offsetX, event.offsetY)

				//viewToProject: gives the coordinates in the Project space from the Screen Coordinates
				let viewPosition = this.scope.view.viewToProject(mousePosition)

				var mpos = viewPosition
				var ctr = this.scope.view.center

				var pc = mpos.subtract(ctr)
				var offset = mpos.subtract(pc.multiply(beta)).subtract(ctr)

				this.scope.view.zoom = newZoom
				this.scope.view.center = this.scope.view.center.add(offset)

				event.preventDefault()

				if (this.scope.view.zoom > 3) {
					this.scope.project.activeLayer.children[0].children[2].visible = true
					this.scope.project.activeLayer.children[0].children[3].visible = true
				} else if (this.scope.view.zoom > 1.12) {
					this.scope.project.activeLayer.children[0].children[2].visible = true
					this.scope.project.activeLayer.children[0].children[3].visible = false
				} else {
					this.scope.project.activeLayer.children[0].children[2].visible = false
					this.scope.project.activeLayer.children[0].children[3].visible = false
				}


				this.scope.view.draw()

				this.zoomLevel = this.scope.view.zoom

			}
		},
		mounted() {
			this.scope = new paper.PaperScope()
			this.scope.setup('MapCanvasMain')
			this.scope.view.zoom = 0.5
			this.scope.view.center = new paper.Point(1129, 923)
			this.zoomLevel = this.scope.view.zoom
			let self = this
			this.scope.project.importSVG(islands, {
				onLoad: (item) => {
					self.scope.project.activeLayer.children[0].children[3].visible = false
					self.scope.project.activeLayer.children[0].children[2].visible = false
				}
			})


			// window.addEventListener('mouse')
		}

	}


</script>

