        width: 100%;
        height: 100%;
        margin: 0 0 0 0;
        background-color: #b3fdff;

    }

    canvas[resize] {
        width: 100%;
        height: 100%;
    }

    #debugInfo {
        position: fixed;
        z-index: 101;
        bottom: 50px;
        right: 50px;
        width: 170px;
        height: 100px;
        border-radius: 10px;
        border: 1px solid lightgrey;
        background-color: white;
        padding: 10px 10px 10px 10px;
    }

</style>
